<script setup>
import {onMounted, ref} from "vue"
import {adminStore} from "../../store/adminStore.js";
import {storeToRefs} from "pinia"
import { useToast } from 'primevue/usetoast';

const toast = useToast();



const as = adminStore()
const asr = storeToRefs(as)
const token = ref('')
onMounted(async ()=>{
  await as.checkTable();

  const newToken = await new Promise(resolve => {
    BX.rest.callMethod("mwsproactiverest.getToken",{},res=>{resolve(res.data())})
  })
  if(token){
    token.value = newToken
  }
})
const regenerate = async ()=>{

       const newToken = pass_gen(128)
             token.value = newToken
        const tokens = await new Promise(resolve => {
          BX.rest.callMethod("mwsproactiverest.setToken",{genToken:newToken},res=>{resolve(res.data())})
        })
      if(tokens){
        //TODO Написать плашку  комплита
        toast.add({ severity: 'success', summary: 'Установлен новый токен!', detail: '', life: 3000 });
      }

}



const pass_gen = (len) => {
  let chrs = 'abdehkmnpswxzABDEFGHKMNPQRSTWXZ123456789';
  let str = '';
  for (let i = 0; i < len; i++) {
    let pos = Math.floor(Math.random() * chrs.length);
    str += chrs.substring(pos,pos+1);
  }
    return  str ;
}

const  copy = ()=>{
  var copyText = document.getElementById("copyBuf");
  copyText.value = token.value


  /* Select the text field */
  copyText.select();

  /* Copy the text inside the text field */
  document.execCommand("copy");
}
</script>

<style scoped>
