<template>
  <div class="flex flex-wrap flex-column gap-3">
    <Panel toggleable collapsed="false" class="w-full">
      <template #header>
        Метрики <span> Всего {{ asr.metric.value.length }} метрик</span>
      </template>
      <div class="mb-3">
        <Button label="Добавить" @click="addDialogMetric = !addDialogMetric"/>

      </div>

      <DataTable
                :value="as.getTableMetric"
                 paginator
                 :rows="5"
                :rowsPerPageOptions="[5, 10, 20, 50]"
                 v-model:editingRows="editingRows"
                  v-model:selection="selectedProduct"
                editMode="row"
                dataKey="id"
                @row-edit-save="onRowEditSave"
      >
        <Column selectionMode="multiple" headerStyle="width: 3rem"></Column>
        <Column field="id">
          <template #header>
            Ид
          </template>
          <template #body="slotProps">
            {{ slotProps.data.id }}
          </template>

        </Column>
        <Column field="metricName">
          <template #header>
            Метрика
          </template>
          <template #body="slotProps">
            {{ slotProps.data.metricName }}
          </template>
          <template #editor="{ data, field }">
            <InputText v-model="data[field]"/>
          </template>
        </Column>
        <Column field="metricMeaning">
          <template #header>
            Возможное значение
          </template>
          <template #body="slotProps">
            {{ slotProps.data.metricMeaning }}
          </template>
          <template #editor="{ data, field }">
            <InputText v-model="data[field]"/>
          </template>
        </Column>
        <Column field="metricStandart">
          <template #header>
            Норматив
          </template>
          <template #body="slotProps">
            {{ slotProps.data.metricStandart }}
          </template>
          <template #editor="{ data, field }">
            <InputText v-model="data[field]"/>
          </template>
        </Column>
        <Column field="metricDescription">
          <template #header>
            Описание
          </template>
          <template #body="slotProps">
            <div class="w-20rem word-wrap"
                 v-tooltip="{ value:slotProps.data.metricDescription, showDelay: 1000, hideDelay: 300 }">
              {{ slotProps.data.metricDescription }}
            </div>
          </template>
          <template #editor="{ data, field }">
            <Textarea v-model="data[field]" autoResize rows="5" cols="30"/>
          </template>
        </Column>
        <Column field="metricTests">
          <template #header>
            Регламент тестов
          </template>
          <template #body="slotProps">
            <div class="w-20rem word-wrap"
                 v-tooltip="{ value:slotProps.data.metricTests, showDelay: 1000, hideDelay: 300 }">
              {{ slotProps.data.metricTests }}
            </div>
          </template>
          <template #editor="{ data, field }">
            <Textarea v-model="data[field]" autoResize rows="5" cols="30"/>
          </template>
        </Column>
        <Column :rowEditor="true" style="width: 10%; min-width: 8rem" bodyStyle="text-align:center"></Column>
        <template #empty>
            <span class="align-content-center">
           В данный момент нет не одной метрики.
          </span>
        </template>

      </DataTable>

      <div class="mb-3"  v-if="selectedProduct.length > 0">
        <Button label="Удалить" @click="delDialogMetric = !delDialogMetric"/>

      </div>
    </Panel>

    <Panel header="Привязки к сушностям" toggleable collapsed="false" class="w-full">
      <div class="flex flex-wrap flex-row gap-3 justify-content-start">

        <Card>
          <template #header>
          </template>
          <template #title>Сделки воронка</template>
          <template #subtitle></template>
          <template #content>
            <Listbox :options="lists" optionLabel="name" class="w-14rem" listStyle="max-height:250px"/>


          </template>
          <template #footer>
            <Inplace @close="testEvent(test)" :closable="true" closeIcon='pi pi-check'>
              <template #display>
                {{ 'Добавить' }}
              </template>
              <template #content>
                <InputText v-model="text" autofocus/>
              </template>
            </Inplace>
          </template>
        </Card>
        <Card>
          <template #header>
          </template>
          <template #title>Лицевые счета (смарт процесс)</template>
          <template #subtitle></template>
          <template #content>
            <Listbox :options="lists" optionLabel="name" class="w-14rem" listStyle="max-height:250px"/>


          </template>
          <template #footer>
            <Inplace @close="testEvent(test)" :closable="true" closeIcon='pi pi-check'>
              <template #display>
                {{ 'Добавить' }}
              </template>
              <template #content>
                <InputText v-model="text" autofocus/>
              </template>
            </Inplace>
          </template>
        </Card>
      </div>
    </Panel>

    <Dialog v-model:visible="addDialogMetric" header="Добавить метрику" :style="{ width: '35rem' }" position="topright"
            :modal="true" :draggable="false">
      <span class="p-text-secondary block mb-5">Update your information.</span>
      <div class="flex flex-column  gap-3 mb-3">
        <label for="metricName" class="font-semibold ">Название метрики</label>
        <InputText id="metricName" v-model="metricName" class="flex-auto" autocomplete="off"/>
      </div>
      <div class="flex flex-column gap-3 mb-5">
        <label for="metricMeaning" class="font-semibold">Возможные значения</label>
        <InputText id="metricMeaning" v-model="metricMeaning" class="flex-auto" autocomplete="off"/>
      </div>
      <div class="flex flex-column  gap-3 mb-5">
        <label for="metriStandart" class="font-semibold ">Стандарт</label>
        <InputText id="metricStandart" v-model="metricStandart" class="flex-auto" autocomplete="off"/>
      </div>
      <div class="flex flex-column  gap-3 mb-5">
        <label for="metricDescription" class="font-semibold ">Описание метрики</label>
        <Textarea id="metricDescription" v-model="metricDescription" autoResize rows="5" cols="30"/>
      </div>
      <div class="flex flex-column  gap-3 mb-5">
        <label for="metricTests" class="font-semibold ">Регламент тестов</label>
        <Textarea id="metricTests" v-model="metricTests" autoResize rows="5" cols="30"/>
      </div>
      <div class="flex justify-content-end gap-2">
        <Button type="button" label="Отмена" severity="secondary" @click="addDialogMetric = !addDialogMetric"></Button>
        <Button type="button" label="Сохранить" @click="addDialogMetric = false, saveMetric()"></Button>
      </div>
    </Dialog>
    <Dialog v-model:visible="delDialogMetric" header="Удаление метрик":style="{ width: '35rem' }" position="topright"  :modal="true" :draggable="false">
      <Card   class="w-30rem">
        <template #header>
        </template>
        <template #title>Удалить выбраные Метрики?</template>
        <template #subtitle>Удалятся только метрики данные из АСУО останутся в таблице</template>
        <template #content>


          <div class="flex flex-column">
      <div class="flex align-items-center justify-content-between h-3rem font-bold border-round m-2 p-2" v-for="table in selectedProduct" ><span>{{table.id}}</span> <span>{{table.metricName}}</span>  </div>
          </div>
        </template>
         <template #footer>
           <div class="flex justify-content-end gap-2">
             <Button type="button" label="Отмена" severity="secondary" @click="delDialogMetric = !delDialogMetric , selectedProduct = []"></Button>
             <Button type="button" label="Удалить" @click="delDialogMetric = false, delMetric()"></Button>
           </div>
         </template>
      </Card>
    </Dialog>

  </div>
</template>

<script setup>
import {onMounted, ref} from "vue";
import {adminStore} from "../../store/adminStore.js";
import {storeToRefs} from "pinia";
import {useToast} from 'primevue/usetoast';

const toast = useToast();
const as = adminStore()
const asr = storeToRefs(as)
const addDialogMetric = ref(false)
const delDialogMetric = ref(false)

const metricName = ref('')
const metricMeaning = ref('')
const metricStandart = ref('')
const metricDescription = ref('')
const metricTests = ref('')
const editingRows = ref([]);
const selectedProduct = ref([]);
const saveMetric = async () => {
  const fields = {
    "NAME": metricName.value,
    "MEANING": metricMeaning.value,
    "STANDART": metricStandart.value,
    "DESCRIPTION": metricDescription.value,
    "TESTS": metricTests.value
  }
  const res = await as.setMetric(fields)
  if (res > 0) {
    toast.add({severity: 'success', summary: 'Метрика успешно добавлена', detail: '', life: 3000});
  } else {
    toast.add({severity: 'error', summary: 'Неудалось добавить метрику', detail: '', life: 3000});
  }
}
const onRowEditSave = async (event) => {
  let {newData, index} = event;

  asr.metric.value[index] = newData;

  const result = await as.updateMetric(newData)
  if (result) {
    toast.add({severity: 'success', summary: 'Метрика успешно добавлена', detail: '', life: 3000});
  } else {
    toast.add({severity: 'error', summary: 'Неудалось добавить метрику', detail: '', life: 3000});
  }
};
const delMetric =   ()=>{
  selectedProduct.value.forEach(async elem=>{
        const res = await as.deleteMetric(elem)
          if(res){
            toast.add({severity: 'success', summary: 'Метрика успешно удалена', detail: '', life: 3000});
          } else {
            toast.add({severity: 'error', summary: 'Неудалось удалить метрику', detail: '', life: 3000});
          }
  })
}
onMounted(async () => {
  const category = await as.getDealCategory()
  await as.getMetric()

})

</script>

<style scoped>
.word-wrap {
  height: 38px;
  word-wrap: break-word;
  overflow: hidden;
}
</style>